import React from 'react';
import Snake from 'react-simple-snake'
import { useState } from "react";
import { Button } from 'rbx';

const PlaySnake = () => {
  const [play, setPlay] = useState('false')
  const [button, showPlayButton] = useState('true')

  function clickPlay() {
    setPlay("true")
    showPlayButton('false')
  }
  function clickStop() {
    showPlayButton('true')
    setPlay("false")
  }
  return (
    <section id="snake">
      {button === "true" && (
        <>
          <h3> Press button to play snake</h3>
          <Button
            style={{ backgroundColor: "black", color: "white" }}
            onClick={clickPlay}
          >
            Play Snake
          </Button>
        </>
      )}
      {play === "true" && (
        <>
          <h3>Use W/A/S/D keys to play snake below:</h3>
          <Snake
            style={{ marginBottom: 100 }}
            percentageWidth="55"
            startSnakeSize="5"
            appleColor="red"
            snakeColor="green"
          />
        </>
      )}
      <br />
      <br />
      {button === "false" && (
        <Button style={{ backgroundColor: "black", color: "white" }} onClick={clickStop}>
          Stop Snake
        </Button>
      )}
    </section>
  );
}

export default PlaySnake;